
* {
  -webkit-touch-callout: none;
  /* prevent callout to copy image, etc when tap to hold */
  -webkit-text-size-adjust: none;
  /* prevent webkit from resizing text to fit */
  -webkit-tap-highlight-color: transparent;
  /* prevent tap highlight color / shadow */
  line-height: 1.2em;
}
html, body {
  overflow: hidden;
}
#App {
  font: 16px monospace;
  position: relative;
  width: 100vw;
  min-height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  overflow: hidden;
  color: #444;
}
a {
  color: #00bbd0;
}
.mainview {
  background: #fff;
  height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.slide-left-enter-active, .slide-left-leave-active, .slide-right-enter-active, .slide-right-leave-active {
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  position: absolute;
  background: #fff;
  width: 100vw;
  min-height: 100vh;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.slide-left-enter-active, .slide-right-enter-active {
  z-index: 11;
}
.slide-left-leave-active:after, .slide-right-leave-active:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100%;
  z-index: 10;
  background: rgba(60, 120, 120, 0.7);
  opacity: 0;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.slide-left-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-left-leave-to:after, .slide-right-leave-to:after {
  opacity: 1;
}
.plr {
  padding-left: 4vw;
  padding-right: 4vw;
}
@media screen and (min-width: 1020px) {
.plr {
    padding-left: 30px;
    padding-right: 30px;
}
}
.form {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
}
.form-signin {
  margin-top: calc(4vw + 1em);
  background: #efefef;
  outline: 4vw solid #efefef;
}
@media screen and (min-width: 1020px) {
.form-signin {
    margin-top: 60px;
    outline-width: 30px;
}
}
.form-row {
  margin-bottom: 1em;
}
.buttons {
  margin-top: 1.5em;
}
input, textarea {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: .5em;
  border-radius: 3px;
  border: 1px solid #ccc;
  -webkit-transition: all .1s ease;
  transition: all .1s ease;
  outline: none !important;
}
.button, button {
  display: inline-block;
  width: 100%;
  border: 0;
  border-radius: 3px;
  background: #00bbd0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: .5em 1em;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: all .1s ease;
  transition: all .1s ease;
  outline: none !important;
}
input:active, button:active, .button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
label {
  font-size: .8em;
  color: #666;
  display: block;
  margin-bottom: .2em;
}
.form-note {
  color: #666;
  font-size: .9em;
  margin-top: .5em;
}
.form-error {
  font-size: .8em;
  color: #079db7;
  margin-top: .5em;
}
input[disabled] {
  background: #f0f0f0;
}
table {
  border-collapse: collapse;
}
th {
  background: #f0f0f0;
}
td, th {
  border: 1px solid #ddd;
  padding: .5em;
}
.center {
  text-align: center;
}
button.small, .button.small {
  display: inline-block;
  width: auto;
  padding: .3em 1em;
  background: #f0f0f0;
  font-weight: 400;
}
button.yellow, .yellow {
  background: #ffd400;
  color: #000;
}
button.red, .red {
  background: #ff4040;
  color: #fff;
  font-weight: 700;
}
button.grey, .grey {
  background: #f0f0f0;
  color: #000;
}
button.white, .white {
  background: #fff;
  color: #000;
}
button.deny, .deny {
  background: #fff;
  color: #555;
}
.button-small-red {
  display: inline-block;
  width: auto;
  padding: .3em 1em;
  background: #ff4040;
  font-weight: 400;
}
.show-enter-active, .show-leave-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  overflow: hidden;
}
.show-leave, .show-enter-to {
  max-height: 100vh;
  opacity: 1;
}
.show-enter, .show-leave-to {
  max-height: 0;
  opacity: 0;
}
.show-del1-enter-active, .show-del1-leave-active {
  overflow: hidden;
}
.show-del1-leave, .show-del1-enter-to {
  -webkit-animation: show-del1-enter .5s .5s linear;
          animation: show-del1-enter .5s .5s linear;
}
.show-del1-enter, .show-del1-leave-to {
  -webkit-animation: show-del1-leave .5s .5s linear;
          animation: show-del1-leave .5s .5s linear;
}
@-webkit-keyframes show-del1-enter {
100% {
    max-height: 100vh;
    opacity: 1;
}
}
@keyframes show-del1-enter {
100% {
    max-height: 100vh;
    opacity: 1;
}
}
@-webkit-keyframes show-del1-leave {
100% {
    max-height: 0;
    opacity: 0;
}
}
@keyframes show-del1-leave {
100% {
    max-height: 0;
    opacity: 0;
}
}
.pull-down-container {
  overflow: auto;
}
.pull-down-header {
  background-color: #eee !important;
  max-height: 60px;
}
.refresh .pull-down-header {
  height: 60px !important;
}
.refresh .pull-down-content--icon {
  -webkit-animation: rotate 1s linear infinite;
          animation: rotate 1s linear infinite;
}
@-webkit-keyframes rotate {
0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(540deg);
            transform: rotate(540deg);
}
}
@keyframes rotate {
0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(540deg);
            transform: rotate(540deg);
}
}
.pull-down-content {
  color: #666;
}
.show-del2-enter-active {
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  -webkit-animation: show-del2-enter .5s .5s ease forwards;
          animation: show-del2-enter .5s .5s ease forwards;
}
.show-del2-leave-active {
  -webkit-animation: show-del2-leave .5s ease;
          animation: show-del2-leave .5s ease;
}
.show-del2-leave-active #Send-textarea {
  height: 1.8em;
}
@-webkit-keyframes show-del2-enter {
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes show-del2-enter {
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes show-del2-leave {
100% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
}
}
@keyframes show-del2-leave {
100% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
}
}

